import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isAdministration: false,
    toolbar: null
  },
  mutations: {
  },
  actions: {
    setAdministration({state}, administration) {
      state.isAdministration = administration;
    },
    setToolbar( {state}, text) {
      state.toolbar = text;
    }
  },
  modules: {
  }
})
