<template>
  <div>
    <v-row>
      <v-col>
        <v-card>
          <v-card-title>
            {{ $t("administration.attendenceOverview") }}
          </v-card-title>
          <v-card-text>
            <div class="text--primary">
              <v-row>
                <v-col>
                  {{ $t("employee") }}
                </v-col>
                <v-col>
                  <v-select
                    v-model="selectedEmployee"
                    :items="employees"
                    item-text="firstName"
                    item-value="id"
                    :label="$t('employee')"
                  >
                    <template slot="item" slot-scope="data">
                      {{ data.item.firstName }} {{ data.item.lastName }} -
                      {{ data.item.department.name }}
                    </template>
                    <template slot="selection" slot-scope="data">
                      {{ data.item.firstName }} {{ data.item.lastName }} -
                      {{ data.item.department.name }}
                    </template>
                  </v-select>
                </v-col>
              </v-row>
              <v-row>
                <v-col>
                  {{ $t("period") }}
                </v-col>
                <v-col>
                  <v-dialog
                    ref="dialog"
                    v-model="period.modal"
                    :return-value.sync="period.date"
                    persistent
                    width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="period.date"
                        :label="$t('selectPeriod')"
                        prepend-icon="mdi-calendar"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-date-picker
                      v-model="period.date"
                      type="month"
                      scrollable
                      locale="sk"
                    >
                      <v-spacer></v-spacer>
                      <v-btn text color="primary" @click="period.modal = false">
                        Cancel
                      </v-btn>
                      <v-btn
                        text
                        color="primary"
                        @click="$refs.dialog.save(period.date)"
                      >
                        OK
                      </v-btn>
                    </v-date-picker>
                  </v-dialog>
                </v-col>
              </v-row>
              <v-row>
                <v-col>
                  <v-btn @click="getOvierviewSelection()" color="#1e88e5" class="white--text">{{$t('viewReport')}}</v-btn>
                </v-col>
              </v-row>
            </div>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col> </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-data-table
          :headers="attendanceHeader"
          :items="attendance"
          :items-per-page="20"
          class="elevation-1"
        >
          <template v-slot:[`item.user`]="{ item }">
            <span
              ><router-link
                :to="{ name: 'UserAttendance', params: { id: item.user.id } }"
                >{{ item.user.firstName }} {{ item.user.lastName }}</router-link
              ></span
            >
          </template>
          <template v-slot:[`item.department`]="{ item }">
            <span>{{ item.user.department.name }}</span>
          </template>
          <template v-slot:[`item.day`]="{ item }">
            <span>{{ (getDayName(item.dateAt)) }}</span>
          </template>
          <template v-slot:[`item.arrivalAt`]="{ item }">
            <span v-if="item.arrivalAt != null">{{
              item.arrivalAt | moment("DD.MM.YYYY HH:MM:ss")
            }}</span>
          </template>
          <template v-slot:[`item.leaveAt`]="{ item }">
            <span v-if="item.leaveAt != null">{{
              item.leaveAt | moment("DD.MM.YYYY HH:MM:ss")
            }}</span>
          </template>
          <template v-slot:[`item.lunchAt`]="{ item }">
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <span v-if="item.lunchAt != null" v-bind="attrs" v-on="on">{{$t('yes')}}</span>
                <span v-else >{{$t('no')}}</span>
              </template>
              <span v-if="item.lunchAt != null">{{item.lunchAt | moment("HH:MM:ss")}}</span>
            </v-tooltip>
          </template>
          <template v-slot:[`item.complet`]="{ item }">
            <span> {{ completedTime(item.arrivalAt, item.leaveAt) }}</span>
          </template>
          <template v-slot:[`item.worked`]="{ item }">
            <span> {{ workedTime(item.arrivalAt, item.leaveAt, item.lunchAt) }}</span>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  name: "AttendenceOverview",
  components: {},
  data() {
    return {
      attendance: [],
      attendanceHeader: [
        {
          text: this.$t("name"),
          align: "start",
          sortable: true,
          value: "user",
        },
        { text: this.$t("department"), value: "department" },
        { text: this.$t("day"), value: "day" },
        { text: this.$t("arrival"), value: "arrivalAt" },
        { text: this.$t("leaving"), value: "leaveAt" },
        { text: this.$t("lunch"), value: "lunchAt" },
        { text: this.$t("complet"), value: "complet"},
        { text: this.$t('worked'), value: "worked"}
      ],
      departments: [],
      selectedEmployee: null,
      employees: [],
      period: {
        date: new Date().toISOString().substr(0, 7),
        modal: false,
      },
    };
  },
  mounted() {
    this.$store.dispatch('setToolbar', this.$t("administration.attendenceOverview"));
    this.getAttendance();
    this.getUsers();
  },
  methods: {
    getAttendance() {
      this.$axios
        .get("/api/attendance/")
        .then((response) => {
          console.log(response);
          this.attendance = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getDepartments() {
      this.$axios
        .get("/api/department/")
        .then((response) => {
          console.log(response);
          this.departments = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getUsers() {
      this.$axios.get("/api/user/").then((response) => {
        console.log("All users");
        console.log(response);
        if (response.data != null) {
          this.employees = response.data;
        }
      });
    },
    getOvierviewSelection() {
      var request = {
        employeeId: this.selectedEmployee,
        period: this.period.date
      }
      this.$axios.post("/api/attendance/overviewSelect", request).then((response) => {
        console.log("Select Ovierview");
        console.log(response);
        this.attendance = response.data;
      });
    },
    completedTime(time1, time2) {
      var date1 = this.$moment(time1);
      var date2 = this.$moment(time2);
      var minutesDiff = date2.diff(date1, 'minutes');

      var hours = (minutesDiff / 60);
      var rhours = Math.floor(hours);
      var minutes = (hours - rhours) * 60;
      var rminutes = Math.round(minutes);
      return rhours + "h "+ rminutes +"m";
    },
    workedTime(time1, time2, lunch) {
      if(lunch != null) {
        var date1 = this.$moment(time1);
        var date2 = this.$moment(time2);
        var minutesDiff = date2.diff(date1, 'minutes');
        minutesDiff = minutesDiff - 30;
        var hours = (minutesDiff / 60);
        var rhours = Math.floor(hours);
        var minutes = (hours - rhours) * 60;
        var rminutes = Math.round(minutes);
        return rhours + "h "+ rminutes +"m";
      } else {
        return this.completedTime(time1, time2);
      }
    },
    getDayName(date) {
      var dt = this.$moment(date);
      
      return this.$t(dt.format('dddd'));
    }
  },
  computed: {
  }
};
</script>
