<template>
  <div>
    <v-row>
      <v-col>
        <v-data-table
          :headers="attendanceHeader"
          :items="attendance"
          :items-per-page="20"
          class="elevation-1"
        >
          <template v-slot:[`item.user`]="{ item }">
            <span><router-link :to="{ name: 'UserAttendance', params: { id: item.user.id }}">{{ item.user.firstName }} {{ item.user.lastName }}</router-link></span>
          </template>
          <template v-slot:[`item.department`]="{ item }">
            <span>{{ item.user.department.name }}</span>
          </template>
          <template v-slot:[`item.arrivalAt`]="{ item }">
            <span v-if="item.arrivalAt != null">{{
              item.arrivalAt | moment("DD.MM.YYYY HH:MM:ss")
            }}</span>
          </template>
          <template v-slot:[`item.leaveAt`]="{ item }">
            <span v-if="item.leaveAt != null">{{
              item.leaveAt | moment("DD.MM.YYYY HH:MM:ss")
            }}</span>
          </template>
          <template v-slot:[`item.lunchAt`]="{ item }">
            <span v-if="item.lunchAt != null">{{
              item.lunchAt | moment("DD.MM.YYYY HH:MM:ss")
            }}</span>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </div>
</template>

<script>

export default {
  name: 'OverviewOfApproaches',
  components: {
  },
  data() {
    return {
      attendance: [],
      attendanceHeader: [
        {
          text: this.$t("name"),
          align: "start",
          sortable: true,
          value: "user",
        },
        { text: this.$t("department"), value: "department" },
        { text: this.$t("arrival"), value: "arrivalAt" },
        { text: this.$t("leaving"), value: "leaveAt" },
        { text: this.$t("lunch"), value: "lunchAt" },
      ],
      departments: [],
      
    };
  },
  mounted() {
    this.$store.dispatch('setToolbar', this.$t("administration.overviewOfApproaches"));
    this.getAttendance();
  },
  methods: {
    getAttendance() {
      this.$axios
        .get("/api/attendance/")
        .then((response) => {
          console.log(response);
          this.attendance = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getDepartments() {
      this.$axios
        .get("/api/department/")
        .then((response) => {
          console.log(response);
          this.departments = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
}
</script>