<template>
    <v-container>
        <v-row>
            <v-col>
                <v-card @click="arrival($t('homePage.arrival'))" height="100" class="center-vh">
                    <span>{{$t("homePage.arrival")}}</span>
                </v-card>
            </v-col>
            <v-col>
                <v-card @click="leaving($t('homePage.leaving'))" height="100" class="center-vh">
                    <span>{{$t("homePage.leaving")}}</span>
                </v-card>
            </v-col>
        </v-row>
        <v-row>
            <v-col>
                <v-card @click="doctor($t('homePage.doctor'))" height="100" class="center-vh">
                    <span>{{$t("homePage.doctor")}}</span>
                </v-card>
            </v-col>
            <v-col>
                <v-card @click="vacation($t('homePage.vacation'))" height="100" class="center-vh">
                    <span>{{$t("homePage.vacation")}}</span>
                </v-card>
            </v-col>
        </v-row>
        <v-row>
            <v-col>
                <v-card @click="lunch($t('homePage.lunch'))" height="100" class="center-vh">
                    <span>{{$t("homePage.lunch")}}</span>
                </v-card>
            </v-col>
            <v-col>
                <v-card @click="administration()" height="100" class="center-vh">
                    <span>{{$t("homePage.administration")}}</span>
                </v-card>
            </v-col>
        </v-row>

        <v-dialog
            v-model="dialog.open"
            persistent
            max-width="290"
            >
                <v-card>
                    <v-card-title class="headline">
                    {{dialog.text}}
                    </v-card-title>
                    <v-card-text class="text--primary">
                        <div>{{dialog.text1}}</div>
                    </v-card-text>
                    <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                        color="green darken-1"
                        text
                        @click="cancelAction()"
                    >
                        {{$t('homePage.cancel')}}
                    </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
    </v-container>
</template>


<script>
export default {
    name: 'HomeComponent',
    data() {
        return {
            valid: false,
            nazov: '',
            report: '',
            dialog: {
                open: false,
                text: '',
                text1: '',
                text2: ''
            },
        }
    },
    methods: {
        arrival(text) {
            this.dialog.text = text;
            this.dialog.text1 = '';
            console.log("Arrival");
            this.dialog.open = true;

            this.$axios.get('/api/main/arrival/' + localStorage.attentence_room).then((response) => {
                console.log(response);
                 if(response.data != null) {
                     console.log(response.data);
                     this.dialog.text = text + ": "+ response.data.employeeName;
                     this.dialog.text1 = this.$t('homePage.comingToWorkAt')+ " " + this.$moment(response.data.time).format('HH:mm:ss');
                     //this.dialog.open = false;
                    setTimeout(()=>{
                        this.dialog.open = false;
                    },2500);
                 }
            }).catch((error) => {
                console.log(error);
            });
        },
        leaving(text) {
            this.dialog.text = text;
            this.dialog.text1 = '';
            console.log("Leaving");
            this.dialog.open = true;

            this.$axios.get('/api/main/leaving/' + localStorage.attentence_room).then((response) => {
                console.log(response);
                 if(response.data != null) {
                     console.log(response.data);
                     this.dialog.text = text + ": "+ response.data.employeeName;
                     this.dialog.text1 = this.$t('homePage.leavingWorkAt')+ " " + this.$moment(response.data.time).format('HH:mm:ss');
                     //this.dialog.open = false;
                    setTimeout(()=>{
                        this.dialog.open = false;
                    },2500);
                 }
            }).catch((error) => {
                console.log(error);
            });
        },
        doctor(text){
            this.dialog.text = text;
            this.dialog.text1 = '';
            console.log("Doctor");
            this.dialog.open = true;
        },
        vacation(text) {
            this.dialog.text = text;
            this.dialog.text1 = '';
            console.log("Vacation");
            this.dialog.open = true;
        },
        lunch(text) {
            this.dialog.text = text;
            this.dialog.text1 = '';
            console.log("Lunch");
            this.dialog.open = true;

            this.$axios.get('/api/main/lunch/' + localStorage.attentence_room).then((response) => {
                console.log(response);
                 if(response.data != null) {
                     console.log(response.data);
                     this.dialog.text = text + ": "+ response.data.employeeName;
                     this.dialog.text1 = this.$t('homePage.lunch')+ " " + this.$moment(response.data.time).format('HH:mm:ss');
                     //this.dialog.open = false;
                    setTimeout(()=>{
                        this.dialog.open = false;
                    },2500);
                 }
            }).catch((error) => {
                console.log(error);
            });
        },
        administration(){
            console.log("Administration");
            this.$router.push({path: 'administration'});
        },
        cancelAction() {
            var json = {
                room: localStorage.attentence_room,
                token: null
            }
            this.$axios.post('/api/main/remove-action', json ).then((response) => {
                console.log(response);
                 if(response.data != null) {
                     console.log(response.data);
                     this.dialog.open = false;
                 }
            }).catch((error) => {
                console.log(error);
            });
            
        }
    },
    mounted() {
        console.log("Set admin false")
        this.$store.dispatch('setAdministration', false);
    },
    created() {
        console.log("Set admin false")
        //this.$store.dispatch('setAdministration', 'true');
  }
}
</script>