<template>
  <div>
    <v-card>
      <v-row class="ml-2 mr-2">
        <v-col>
          <v-select
            v-model="filter.department"
            :items="departments"
            item-text="name"
            item-value="id"
            :label="$t('department')"
            return-object
          ></v-select>
        </v-col>
        <v-col>
          <v-menu
            ref="menu"
            v-model="arrivalFromMenu"
            :close-on-content-click="false"
            :nudge-right="40"
            :return-value.sync="filter.arrivalFrom"
            transition="scale-transition"
            offset-y
            max-width="290px"
            min-width="290px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="filter.arrivalFrom"
                :label="$t('arrivalFrom')"
                prepend-icon="mdi-clock-time-four-outline"
                readonly
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-time-picker
              v-if="arrivalFromMenu"
              v-model="filter.arrivalFrom"
              full-width
              format="24hr"
              @click:minute="$refs.menu.save(filter.arrivalFrom)"
            ></v-time-picker>
          </v-menu>
        </v-col>
        <v-col>
          <v-menu
            ref="menu1"
            v-model="arrivalToMenu"
            :close-on-content-click="false"
            :nudge-right="40"
            :return-value.sync="filter.arrivalTo"
            transition="scale-transition"
            offset-y
            max-width="290px"
            min-width="290px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="filter.arrivalTo"
                :label="$t('arrivalTo')"
                prepend-icon="mdi-clock-time-four-outline"
                readonly
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-time-picker
              v-if="arrivalToMenu"
              v-model="filter.arrivalTo"
              full-width
              format="24hr"
              @click:minute="$refs.menu1.save(filter.arrivalTo)"
            ></v-time-picker>
          </v-menu>
        </v-col>
        <v-col>
          <v-menu
            ref="menu2"
            v-model="departureFromMenu"
            :close-on-content-click="false"
            :nudge-right="40"
            :return-value.sync="filter.departureFrom"
            transition="scale-transition"
            offset-y
            max-width="290px"
            min-width="290px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="filter.departureFrom"
                :label="$t('departureFrom')"
                prepend-icon="mdi-clock-time-four-outline"
                readonly
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-time-picker
              v-if="departureFromMenu"
              v-model="filter.departureFrom"
              full-width
              format="24hr"
              @click:minute="$refs.menu2.save(filter.departureFrom)"
            ></v-time-picker>
          </v-menu>
        </v-col>
        <v-col>
          <v-menu
            ref="menu3"
            v-model="departureToMenu"
            :close-on-content-click="false"
            :nudge-right="40"
            :return-value.sync="filter.departureTo"
            transition="scale-transition"
            offset-y
            max-width="290px"
            min-width="290px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="filter.departureTo"
                :label="$t('departureTo')"
                prepend-icon="mdi-clock-time-four-outline"
                readonly
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-time-picker
              v-if="departureToMenu"
              v-model="filter.departureTo"
              full-width
              format="24hr"
              @click:minute="$refs.menu3.save(filter.departureTo)"
            ></v-time-picker>
          </v-menu>
        </v-col>
        <v-col >
          <v-btn @click="doFilter()">
            Filter
          </v-btn>
          <v-btn @click="clearFilter()">
            {{ $t("clearFilter") }}
          </v-btn>
        </v-col>
      </v-row>
    </v-card>

    <v-row>
      <v-col>
        <v-data-table
          :headers="headers"
          :items="tableData"
          :items-per-page="20"
          class="elevation-1"
        >
          <template v-slot:[`item.user`]="{ item }">
            <span>{{ item.user.firstName }} {{ item.user.lastName }}</span>
          </template>
          <template v-slot:[`item.department`]="{ item }">
            <span>{{ item.user.department.name }}</span>
          </template>
          <template v-slot:[`item.arrivalAt`]="{ item }">
            <span v-if="item.arrivalAt != null">{{
              item.arrivalAt | moment("HH:mm:ss")
            }}</span>
          </template>
          <template v-slot:[`item.leaveAt`]="{ item }">
            <span v-if="item.leaveAt != null">{{
              item.leaveAt | moment("HH:mm:ss")
            }}</span>
          </template>
          <template v-slot:[`item.lunchAt`]="{ item }">
            <span v-if="item.lunchAt != null">{{
              item.lunchAt | moment("HH:mm:ss")
            }}</span>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  name: "DayLog",
  components: {},
  data() {
    return {
      headers: [
        {
          text: this.$t("name"),
          align: "start",
          sortable: true,
          value: "user",
        },
        { text: this.$t("department"), value: "department" },
        { text: this.$t("arrival"), value: "arrivalAt" },
        { text: this.$t("leaving"), value: "leaveAt" },
        { text: this.$t("lunch"), value: "lunchAt" },
      ],
      tableData: [],
      allData: [],
      departments: [],
      filter: {
        department: null,
        arrivalFrom: null,
        arrivalTo: null,
        departureFrom: null,
        departureTo: null,
      },
      arrivalFromMenu: false,
      arrivalToMenu: false,
      departureFromMenu: false,
      departureToMenu: false,
    };
  },
  mounted() {
    this.$store.dispatch('setToolbar', this.$t("administration.dayLog"));
    this.getUsers();
    this.getDepartments();
  },
  methods: {
    getUsers() {
      this.$axios
        .get("/api/day-log/")
        .then((response) => {
          console.log(response);
          this.tableData = response.data;
          this.allData = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getDepartments() {
      this.$axios
        .get("/api/department/")
        .then((response) => {
          console.log(response);
          this.departments = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    doFilter() {
      console.log(this.filter);
      this.$axios
        .post("/api/day-log/filter", this.filter)
        .then((response) => {
          console.log("After filter request");
          console.log(response);
          this.tableData = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    clearFilter() {
      (this.filter.department = null), (this.filter.arrivalFrom = null);
      this.filter.arrivalTo = null;
      this.filter.departureFrom = null;
      this.filter.departureTo = null;
      this.tableData = this.allData;
    },
  },
};
</script>
